<template>
    <section class="notice_area">
        <h2 class="main_title">자유게시판</h2>

        <table class="notice_board">
            <thead>
                <tr>
                    <th></th>
                    <th>제목</th>
                    <th>작성자</th>
                    <th>날짜</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="data in noticeData"
                v-bind:key="data.id">
                    <td>{{ data.id }}</td>
                    <td><a v-bind:href="$store.state.host + '/notice/' + data.id" class="notice_title">{{ data.title }}</a></td>
                    <td>{{ data.user_id }}</td>
                    <td>{{ formatDate(data.created_at) }}</td>
                </tr>
            </tbody>
            <tfoot>
                <tr>
                    <td colspan="4"><a href="#" class="more_ranking">더보기</a></td>
                </tr>
            </tfoot>
        </table>
    </section>
</template>

<script>
export default {
    name: 'noticeBoard',
    data () {
        return {
            noticeData: {}
        }
    },
    created () {
        this.getNotice()
    },
    methods: {
        getNotice () {
            this.$axios.get(`${this.$store.state.host}/api/notice`).then(
                res => {
                    this.noticeData = res.data.data
                },
                err => {
                    console.log(err)
                }
            )
        },
        formatDate (date) {
            const formatDate = new Date(date)
            const getYear = formatDate.getFullYear()
            const getMonth = formatDate.getMonth() + 1
            const getDate = formatDate.getDate()
            const getHours = formatDate.getHours()
            const getMunutes = formatDate.getMinutes()

            return `${getYear}-${getMonth}-${getDate} ${getHours}:${getMunutes}`
        }
    }
}
</script>

<style lang="scss" scoped>
@import "@/assets/styles/components/_board.scss";

.notice_area{
    @include board();

    .main_title{
        font-weight: bold;
        font-size: $font-size-base;
        color: $bg-color-black;
    }
}
</style>
