<template>
    <article id="login">
        <h1>Login</h1>
        <form v-on:submit.prevent="login">
            <input type="text" id="id" v-model="user.id">
            <input type="password" id="password" v-model="user.password">
            <input type="submit" value="login">
        </form>
    </article>
</template>

<script>
export default {
    name: 'Login',
    data () {
        return {
            user: {
                id: '',
                password: ''
            }
        }
    },
    created () {
        this.$store.commit('logout')
    },
    methods: {
        login () {
            this.$axios.post(`${this.$store.state.host}/api/users/login`, {
                user: this.user
            })
                .then(
                    res => {
                        alert(res.data.message)

                        if (res.data.status === 200) {
                            this.$store.commit('loginToken', res.data.token)
                            this.$router.push('/')
                        }
                    }
                )
                .catch(err => {
                    alert(err)
                })
        }
    }
}
</script>

<style scoped>
    #login{
        width: 500px;
        margin: 200px auto;
        padding: 20px;
        border: 1px solid #cccccc;
        background-color: #ffffff;
    }

    h1{
        margin-bottom: 12px
    }

    input{
        display: block;
        margin-bottom: 12px;
    }
</style>
