<template>
    <section class="slide_area">
        <h2 class="main_title">슬라이더 영역</h2>

        <div class="slide_container"
        v-on:mouseover="stopEvent"
        v-on:mouseout="startEvent">
            <transition-group name="fade" tag="div" class="slide_list">
                <div class="slide_item"
                v-for="number in [slideIndex]"
                v-bind:key="number">
                    <img v-bind:src="currentImage">
                </div>
            </transition-group>

            <div class="slide_btn_wrap">
                <button type="button" class="slide_btn_prev" v-on:click="prevEvent">prev</button>
                <button type="button" class="slide_btn_next" v-on:click="nextEvent">next</button>
            </div>
        </div>
    </section>
</template>

<script>
export default {
    name: 'slide',
    data () {
        return {
            slideItems: [
                {
                    id: null,
                    item: null
                }
            ],
            slideIndex: 0,
            slideTimer: null,
            slideSpeed: 4000
        }
    },
    created () {
        this.$axios.get('/api/slides').then(response => {
            this.slideItems = response.data
        })
    },
    computed: {
        currentImage: function () {
            return this.slideItems[Math.abs(this.slideIndex) % this.slideItems.length].item
        }
    },
    mounted: function () {
        this.startEvent()
    },
    methods: {
        startEvent: function () {
            this.slideTimer = setInterval(this.nextEvent, this.slideSpeed)
        },
        stopEvent: function () {
            clearInterval(this.slideTimer)
            this.slideTimer = null
        },
        nextEvent: function () {
            if (this.slideIndex + 1 >= this.slideItems.length) {
                this.slideIndex = 0
                return false
            }
            this.slideIndex += 1
        },
        prevEvent: function () {
            if (this.slideIndex <= 0) {
                this.slideIndex = 3
                return false
            }
            this.slideIndex -= 1
        }
    }
}
</script>

<style lang="scss" scoped>
@import "@/assets/styles/components/_slide.scss";
</style>
