<template>
    <article id="NoticeCreate">
        <h1>공지사항 작성</h1>
        <form v-on:submit.prevent="noticeCreate">
            <input type="text" id="title" v-model="title">
            <textarea id="contents" v-model="contents"></textarea>
            <input type="submit" value="작성">
        </form>
    </article>
</template>

<script>
export default {
    name: 'NoticeCreate',
    data () {
        return {
            userId: '',
            title: '',
            contents: ''
        }
    },
    created () {
        this.userId = this.$store.state.user.id
    },
    mounted () {
    },
    methods: {
        noticeCreate () {
            this.$axios.post(`${this.$store.state.host}/api/notice/create`, {
                userId: this.userId,
                title: this.title,
                contents: this.contents
            }).then(res => {
                alert(res.data.message)
            }).catch(err => {
                alert(err)
            })
        }
    }
}
</script>

<style scoped>
    #NoticeCreate{
        width: 600px;
        margin: 200px auto;
        padding: 20px;
        border: 1px solid #cccccc;
        background-color: #ffffff;
    }

    h1{
        margin-bottom: 12px
    }

    input{
        display: block;
        margin-bottom: 12px;
    }

    textarea{
        display: block;
        width: 100%;
        height: 200px;
        margin-bottom: 12px;
        resize: none;
    }
</style>
