import Vue from 'vue'
import Vuex from 'vuex'
import router from '../router'
import axios from 'axios'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

export default new Vuex.Store({
    plugins: [createPersistedState({
        storage: window.sessionStorage
    })],
    // state = 변수
    state: {
        host: 'http://localhost:3000',
        token: '',
        user: {
            id: '',
            name: ''
        }
    },
    // mutations = 변수를 컨트롤 하는 함수
    mutations: {
        loginToken (state, token) {
            state.token = token
        },
        logout (state) {
            if (state.token) {
                state.token = ''
                alert('로그아웃 되었습니다')
            }
        },
        loginCheck (state) {
            axios.get(`${state.host}/api/users/check`, {
                headers: {
                    'x-access-token': state.token
                }
            })
                .then(
                    res => {
                        state.user.id = res.data.token.id
                        state.user.name = res.data.token.name
                    },
                    err => {
                        console.log('계정 정보가 없습니다', err)
                        router.push('/login')
                    }
                )
        }
    },
    actions: {
    },
    modules: {
    }
})
