<template>
    <main class="main">
        <article class="searchArea">
            <h2 class="mainTitle">전적검색</h2>

            <form class="search" v-on:submit.prevent="summonerSearch">
                <input type="text" class="searchName" v-model="summonerName">
                <input type="submit" class="btnSearch" value="검색">
            </form>
        </article>

        <article class="infoArea">
            <h2 class="hidden">랭크 정보</h2>

            <section class="rankWrap" v-for="data in rankData" v-bind:key="data.leagueId">
                <div class="rankImg">
                    <img v-bind:src="tierImage(data.tier, data.rank)" v-bind:alt="data.tier + data.rank.length">
                </div>

                <div class="rankInfo">
                    <div class="rankLeague">{{ data.queueType }}</div>
                    <div class="rankTier">
                        <span class="tier">{{ data.tier }} {{ data.rank }}</span>
                        <span class="point">{{ data.leaguePoints }}LP</span>
                        <span class="series" v-if="data.miniSeries">
                            {{ data.miniSeries.wins }}승 {{ data.miniSeries.losses }}패
                        </span>
                    </div>
                    <div class="rankGame">
                        {{ data.wins }}승 {{data.losses}}패
                    </div>
                </div>
            </section>
        </article>

        <article class="matchArea">
            <h2 class="hidden">전적</h2>

            <div class="matchList" v-if="!isLoading">
                <section class="matchWrap"
                    v-bind:class="data.user.stats.win ? 'win' : 'lose'"
                    v-for="data in matchData"
                    v-bind:key="data.leagueId">
                    <div class="matchStats">
                        <span class="matchType">{{ matchType(data.queue) }}</span>
                        <span class="matchAgo">{{ matchAgo(data.detail.gameCreation) }}</span>
                        <span class="matchResult" v-if="data.user.stats">{{ data.user.stats.win ? '승리' : '패배' }}</span>
                        <span class="matchTime">{{ matchTime(data.detail.gameDuration) }}</span>
                    </div>

                    <div class="settingInfo">
                        <div class="championImg">
                            <div class="image">
                                <img v-bind:src="`/images/champion/${data.champion}.jpg`" alt="">
                            </div>
                        </div>

                        <div class="spell">
                            <div class="image">
                                <img v-bind:src="`/images/spell/${data.user.spell1Id}.png`" alt="">
                            </div>
                            <div class="image">
                                <img v-bind:src="`/images/spell/${data.user.spell2Id}.png`" alt="">
                            </div>
                        </div>

                        <div class="runes">
                            <div class="image">
                                <img v-bind:src="`/images/rune/${data.user.stats.perk0}.png`" alt="">
                            </div>
                            <div class="image">
                                <img v-bind:src="`/images/rune/${data.user.stats.perkSubStyle}.png`" alt="">
                            </div>
                        </div>

                        <div class="championName">
                            <span></span>
                        </div>
                    </div>

                    <div class="kdaInfo">
                        <div class="kda">
                            <span class="kill">{{ data.user.stats.kills }}</span> /
                            <span class="death">{{ data.user.stats.deaths }}</span> /
                            <span class="assist">{{ data.user.stats.assists }}</span>
                        </div>
                        <div class="kdaRatio">
                            <span>{{ kdaRatio(data.user.stats.kills, data.user.stats.deaths, data.user.stats.assists) }}</span>
                            평점
                        </div>
                    </div>

                    <div class="statInfo">
                        <div class="level">
                            레벨 {{ data.user.stats.champLevel }}
                        </div>
                        <div class="cs">
                            <span>{{ data.user.stats.totalMinionsKilled }}</span> CS
                        </div>
                    </div>

                    <div class="itemInfo">
                        <ul class="itemList">
                            <li class="item">
                                <img v-bind:src="`/images/item/${data.user.stats.item0}.png`" alt="">
                            </li>
                            <li class="item">
                                <img v-bind:src="`/images/item/${data.user.stats.item1}.png`" alt="">
                            </li>
                            <li class="item">
                                <img v-bind:src="`/images/item/${data.user.stats.item2}.png`" alt="">
                            </li>
                            <li class="item">
                                <img v-bind:src="`/images/item/${data.user.stats.item6}.png`" alt="">
                            </li>
                            <li class="item">
                                <img v-bind:src="`/images/item/${data.user.stats.item3}.png`" alt="">
                            </li>
                            <li class="item">
                                <img v-bind:src="`/images/item/${data.user.stats.item4}.png`" alt="">
                            </li>
                            <li class="item">
                                <img v-bind:src="`/images/item/${data.user.stats.item5}.png`" alt="">
                            </li>
                        </ul>
                    </div>
                </section>
            </div>
        </article>
    </main>
</template>

<script>
export default {
    name: 'Search',
    data () {
        return {
            summonerName: '',
            summonerInfo: [],
            rankData: [],
            matchData: [],
            isLoading: true
        }
    },
    created () {
    },
    methods: {
        summonerSearch () {
            this.matchData = []
            this.isLoading = true

            this.$axios.post(`${this.$store.state.host}/api/search/id`, {
                summonerName: this.summonerName
            })
                .then(
                    res => {
                        if (res.data.id === '' || res.data.id === null || res.data.id === undefined) {
                            this.summonerInfo = []
                            alert(res.data.message)
                        } else {
                            this.summonerInfo = res.data
                            this.summonerRank(this.summonerInfo.id)
                            this.summonerMatch(this.summonerInfo.accountId)
                        }
                    },
                    err => {
                        console.log(err)
                        alert(err)
                    }
                )
                .catch(err => {
                    alert(err)
                })
        },
        summonerRank (id) {
            this.$axios.post(`${this.$store.state.host}/api/search/rank`, {
                id: id
            })
                .then(
                    res => {
                        this.rankData = res.data
                    },
                    err => {
                        console.log(err)
                        alert(err)
                    }
                )
        },
        summonerMatch (id) {
            this.$axios.post(`${this.$store.state.host}/api/search/match`, {
                id: id
            })
                .then(
                    res => {
                        let count = 0
                        this.matchData = res.data.matches

                        for (const data in this.matchData) {
                            this.summonerMatchDetail(res.data.matches[data]).then(
                                res => {
                                    this.matchData[data].detail = res.data
                                    this.matchData[data].user = this.getUserData(this.matchData[data].detail.participants, this.matchData[data].champion)

                                    return ++count
                                }
                            ).then(
                                res2 => {
                                    if (this.matchData.length === res2) {
                                        this.isLoading = false
                                        console.log(this.matchData[data])
                                    }
                                }
                            )
                        }
                    },
                    err => {
                        console.log(err)
                        alert(err)
                    }
                )
        },
        summonerMatchDetail (match) {
            return this.$axios.post(`${this.$store.state.host}/api/search/match/detail`, {
                gameId: match.gameId,
                queue: match.queue
            })
                .then(
                    res => {
                        return res
                    },
                    err => {
                        console.log(err)
                        alert(err)
                    }
                )
        },
        getUserData (data, value) {
            let result = ''

            data.some(function (element) {
                if (element.championId === value) {
                    result = element
                }

                return (element.championId === value)
            })

            return result
        },
        formatLower (data) {
            data = data.toLowerCase()
            return data
        },
        tierImage (tier, rank) {
            tier = this.formatLower(tier)
            return `/images/tier/${tier}_${rank.length}.png`
        },
        matchType (queue) {
            switch (queue) {
            case 420: {
                return '랭크'
            }
            case 430: {
                return '일반'
            }
            case 440: {
                return '자유 랭크'
            }
            case 450 : {
                return '무작위 총력전'
            }
            default: {
                return ''
            }
            }
        },
        matchAgo (date) {
            const today = new Date()
            date = new Date(date)

            const matchTimeAgo = Math.floor((today.getTime() - date.getTime()) / 1000 / 60)
            if (matchTimeAgo < 1) {
                return '방금전'
            } else if (matchTimeAgo < 60) {
                return `${matchTimeAgo}분전`
            }

            const matchHourAgo = Math.floor(matchTimeAgo / 60)
            if (matchHourAgo < 24) {
                return `${matchHourAgo}시간전`
            }

            const matchDayAgo = Math.floor(matchTimeAgo / 60 / 24)
            if (matchDayAgo < 365) {
                return `${matchDayAgo}일전`
            }
        },
        matchTime (time) {
            const minute = Math.floor(time / 60)
            const second = time % 60

            return `${minute}분 ${second}초`
        },
        kdaRatio (kill = 0, death = 0, assist = 0) {
            if (death === 0) {
                return 'Perfect'
            } else {
                const kda = (kill + assist) / death

                return `${kda.toFixed(2)} : 1`
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import "@/assets/styles/pages/search.scss";
</style>
